import * as React from 'react';
import { Box, Button } from '@chakra-ui/core';
import { useAnimation, motion } from 'framer-motion';

function swipePower(offset, absDistance) {
  return (offset / absDistance) * 100;
}

const variants = {
  toLeft: {
    x: '-100%',
    pointerEvents: 'none',
  },
  toRight: {
    x: '100%',
    pointerEvents: 'none',
  },
  center: {
    x: 0,
    pointerEvents: 'initial',
  },
};

export default function Carousel({ children, width = 500 }) {
  const [index, setIndex] = React.useState(0);
  // const prev = wrap(0, data.length, page - 1);
  // const cur = wrap(0, data.length, page);
  // const next = wrap(0, data.length, page + 1);

  const animation = useAnimation();
  const handleDragEnd = async (evt, { offset }) => {
    const power = swipePower(offset.x, width);
    if (power > 60) {
      await animation.start('toRight');
      paginate(-1);
    } else if (power < -60) {
      await animation.start('toLeft');
      paginate(1);
    }
  };

  const paginate = (dir) => {
    setIndex((index + dir) % children.lenght);
  };

  return (
    <div style={{ width: 500 }}>
      {children.map((item, i) => (
        <motion.div
          key={i}
          style={{ display: 'flex', justifyContent: 'center', alignItems: 'flex-end' }}
          drag="x"
          dragDirectionLock
          dragConstraints={{ left: 0, right: 0 }}
          onDragEnd={handleDragEnd}
          variants={variants}
          animate={animation}
          dragMomentum={false}
          transition={{
            x: { type: 'spring', mass: 0.5, stiffness: 500, damping: 50 },
          }}
        >
          {item}
        </motion.div>
      ))}
    </div>
  );
}
